var TodoApp = React.createClass({
    getInitialState: function () {
        return {
            todos: []
        }
    },

    addTodo: function(item) {
        if (item !== '') {
            var todos = this.state.todos;
            todos.push(item);

            this.setState({
                todos: todos
            });
        }
    },

    render: function(){
        return(
            <div>
                <TodoInput addTodo={this.addTodo} /><br />
                <TodoList todos={this.state.todos} />
            </div>
        );
    },
});

var TodoInput = React.createClass({
    getInitialState: function() {
        return {input: ''}
    },

    changeInput: function(event) {
        this.setState({input: event.target.value});
    },

    add: function(e) {
        e.preventDefault();

        this.props.addTodo(this.state.input);
        // Clear input
        this.setState(this.getInitialState());
    },

    render: function() {
        return (
            <form onSubmit={this.add}>
                <input ref="txtInput" type="text" value={this.state.input} onChange={this.changeInput}  />
                <br />
                <button type="submit">Add</button>
            </form>
        );
    }
});

var TodoList = React.createClass({
    render: function() {
        var todoList = this.props.todos.map(function(todo,index){
            return <li key={index}>{todo}</li>
        });
        return (
            <ul>{todoList}</ul>
        );
    }
});

ReactDOM.render(<TodoApp />, document.getElementById('content'));
